@import "../../style.scss";

.projects{
    @include themify($themes){
        background-color: themed("bgSoft");
        color: themed("textColor");
        padding: 20px 50px;
        min-height: 550px;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        gap: 30px;
        .projCard{
            width: 350px;
            height: 350px;
            background-color: themed("bg");
            padding: 20px;
            border-radius: 20px;
            overflow: hidden;
            cursor: pointer;
            img{
                width: 100%;
                height: 250px;
                object-fit: cover;
                border-radius: 20px 20px 0 0;
            }
            .projTitle{
                font-size: 20px;
                font-weight: 500;
            }
            .projSkills{
                font-size: 14px;
                color: themed("textColorSoft");
            }
        }
    }
}