@import "../../style.scss";

.navbar{
    @include themify($themes){
        background-color: themed("bg");
        color: themed("textColor");
        border-bottom: 1px solid themed("border");
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 20px 50px;
        .logo{
            font-weight: bold;
            font-size: 30px;
            cursor: pointer;
            a{
                color: inherit;
                text-decoration: none;
            }
        }
        .quickLinks{
            display: flex;
            align-items: center;
            gap: 20px;
            span{
                text-transform: uppercase;
                font-size: 14px;
                font-weight: 500;
                cursor: pointer;
                a{
                    color: inherit;
                    text-decoration: none;
                }
            }
        }
    }
}