@import "../../style.scss";

.hero{
    @include themify($themes){
        background-color: themed("bgSoft");
        color: themed("textColor");
        height: 550px;
        padding: 20px 50px;
        position: relative;
        img{
            height: 500px;
            position: absolute;
            bottom: 0;
            right: 50px;
        }
        .heroTitle{
            margin-top: 100px;
            span{
                font-size: 60px;
                font-weight: 200;
                text-transform: uppercase;
            }
            .name{
                font-weight: 500;
            }
        }
        .heroSubtitle{
            font-weight: 500;
            color: themed("textColorSoft");
            margin-bottom: 50px;
        }
        .heroButtons{
            display: flex;
            align-items: center;
            gap: 20px;
            button{
                border: 3px solid themed("textColor");
                color: themed("textColor");
                border-radius: 20px;
                padding: 10px 20px;
                font-weight: bold;
                background: transparent;
                text-transform: uppercase;
                cursor: pointer;
            }
        }
        .heroSocialLinks{
            position: absolute;
            bottom: 50px;
            display: flex;
            align-items: center;
            gap: 20px;
            color: themed("textColorSoft");
            cursor: pointer;
        }
    }
}