@import "../../style.scss";

.footer{
    @include themify($themes){
        background-color: themed("bg");
        color: themed("textColor");
        border-top: 1px solid themed("border");
        padding: 20px 50px;
        height: 70px;
        display: flex;
        flex-direction: column;
        align-items: center;
        font-size: small;
    }
}